import React from "react"
import axios from "axios"
import { Button, Form, Input, Typography } from 'antd'
import {Link} from "react-router-dom"
import RegCSS from "./reg.module.css"

export const Registration = () => {
    document.title = ''

    const OnFinish = (e) => {
        const url = "http://localhost:8080/registration"
        axios.post(url, {
            name: e.name,
            surname: e.surname,
            email: e.email,
            password: e.password
        }).then(response => {
            const code = response.data.code
            if(code === 401) {
                alert(`     `)
            } else if (code === 202) {
                alert(`  `)
                localStorage.setItem('jwt', response.data.jwt)
                window.location.href = '/'
            } else if (code === 204) {
                alert(`   `)
            }
        })
    }

    return(
        <>
            <div className="container">
                <div className={RegCSS.content}>
                    <Form
                        name="Registration"
                        labelCol={{
                            span: 8,
                        }}
                        wrapperCol={{
                            span: 8,
                        }}
                        initialValues={{
                            remember: true,
                        }}
                        onFinish={OnFinish}
                        onFinishFailed={() => {}}
                        autoComplete="on"
                        >
                        <Form.Item
                            label=""
                            name="name"
                            rules={[
                            {
                                required: true,
                                message: ',   !',
                            },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            label=""
                            name="surname"
                            rules={[
                            {
                                required: true,
                                message: ',   !',
                            },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            label=" "
                            name="email"
                            rules={[
                            {
                                required: true,
                                message: ',    !',
                            },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            label=""
                            name="password"
                            rules={[
                            {
                                required: true,
                                message: ',   !',
                            },
                            ]}
                        >
                            <Input.Password />
                        </Form.Item>
                        <Form.Item
                            wrapperCol={{
                            offset: 8,
                            span: 16,
                            }}
                        >
                            <Button type="primary" htmlType="submit">
                                
                            </Button>
                        </Form.Item>
                    </Form>   
                    <p className={RegCSS.isReg}>
                         ?    <Link to="/authorization"></Link>
                    </p> 
                </div>   
            </div>    
        </>
    )
}