import React from "react"
import axios from "axios"
import {Link} from "react-router-dom"
import { Button, Form, Input, Typography } from 'antd'
import LoginCSS from "./login.module.css"

export const Login = () => {
    function OnFinish(e) {
        const url = "http://localhost:8080/login"
        axios.post(url, {
            email: e.email,
            password: e.password
        }).then(response => {
            const code = response.data.code
            if (code === 404) {
                alert(`-   .  `)
            } else if (code === 403) {
                alert(`  `)
            } else if (code === 202) {
                console.log(response.data.jwt)
                alert(` `)
                localStorage.setItem('jwt', response.data.jwt)
                window.location.href = '/'
            }
        })
    }
    return(
        <div className="container">
            <div className={LoginCSS.content}>
                <Form
                    name="Authorization"
                    labelCol={{
                        span: 8,
                    }}
                    wrapperCol={{
                        span: 8,
                    }}
                    initialValues={{
                        remember: true,
                    }}
                    onFinish={OnFinish}
                    onFinishFailed={() => {}}
                    autoComplete="on"
                    >
                    <Form.Item
                        label=" "
                        name="email"
                        rules={[
                        {
                            required: true,
                            message: ',    !',
                        },
                        ]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item
                        label=""
                        name="password"
                        rules={[
                        {
                            required: true,
                            message: ',   !',
                        },
                        ]}
                    >
                        <Input.Password />
                    </Form.Item>
                    <Form.Item
                        wrapperCol={{
                        offset: 8,
                        span: 16,
                        }}
                    >
                        <Button type="primary" htmlType="submit">
                            
                        </Button>
                    </Form.Item>
                </Form>
                <p className={LoginCSS.isReg}>
                       ?    <Link to="/registration"></Link>
                </p>
            </div>
        </div>
    )
}